import React from 'react'
import classes from '../../css/styles.css'
import ReactTooltip from 'react-tooltip'
/**
 * GemeralHelper Clase de ayuda usada para definir funciones generales para el
 *               semáforo compartidas por los distintos componentes
 */
export default class GeneralHelper{
    /**
     * Función enfocada en calcular los espacios en blanco de las cabeceras.
     * @param {Integer} nodesCount Cantidad de nodos.
     * @param {Integer} columnsVisibleInFlags Columnas visibles en pantalla.
     * @return {Integer} Se devuelve la diferencia calculada.
     */
    static calculateEmptyCells(nodesCount, columnsVisibleInFlags){
        //Primero se obtiene la aproximación de la cantidad de nodos visibles
        //según los nodos visibles en pantalla
        //3 -> nodos en pantalla
        //14 -> cantidad total de nodos
        //Ej: ((14/3) * 3) -> 12
        //Entonces: (14 == 12) -> No es igual por lo tanto hay que agregar 1 celda más
        const monitorDiff = (parseInt(nodesCount / columnsVisibleInFlags) * columnsVisibleInFlags)
        const diffValue = (monitorDiff == nodesCount) ? 0 : (
            (monitorDiff + columnsVisibleInFlags) - nodesCount
        )
        return diffValue
    }

    /**
     * Función destinada a crear el JSX de los espacios en blanco.
     * @param {String} nickName Variable que se usa para agregar un identificador
     *                          para el JSX.
     * @param {Integer} emptyCells Cantidad de celdas vacías.
     * @param {Object} blankWidthStyle Estilos para las celdas en blanco.
     * @return {Object} Se retorna variable contenedora del JSX de las celdas en blanco.
     */
    static createBlankSpaces(nickName, emptyCells, blankWidthStyle){
        let emptyCellsContainer = []
        if(emptyCells > 0){
            for (var i = 0; i < emptyCells; i++) {
                emptyCellsContainer.push(<td key={`${nickName}_blank_${i}`} style={blankWidthStyle} className={classes['rows']}>{'\u00A0'}</td>)
            }
        }
        return emptyCellsContainer
    }

    /**
     * Función enfocada en resolver el label para visualizar el color correcto
     * de los sprite de la imagen de los estados de los semáforos.
     * @param {String} status Contiene información del tipo de estado asociado a
     *                        resultado.
     * @return {String} Devuelve un String con el label correspondiente a un color
     *                  según estado.
     */
    static setColorStatus(status){
        let colorStatus
        switch (status) {
            case "uptime":
                colorStatus = "verde"
                break
            case "downtime_global":
                colorStatus = "rojo"
                break
            case "downtime_parcial":
                colorStatus = "amarillo"
                break
            case "sin_informacion":
                colorStatus = "blanco"
                break
            case "evento_cliente":
                colorStatus = "fucsia"
                break
            default:
                colorStatus = ""
                break
        }
        return colorStatus
    }

    /**
     * Función encargada de mostrar un ícono y el tooltip de un estado en caso
     * de que haya una medición correcta.
     * @param {Object} statusDetail Variable que contiene información de la información
                                  de los estados en general.
     * @param {Object} event Variable que contiene información del estado de
     *                            una medición.
     * @param {String} colorStatus Cadena de texto donde se indica el tipo de color
     *                             según estado.
     * @return {Object} Se retorna un JSX con la información del estado y tooltip
     *                  asociado.
     */
    static setFlagIconAndTooltip(statusDetail, event, colorStatus){
        //Se genera un valor aleatorio
        var randomValue = Math.random().toString(36).substr(2)
        //Se comprueba que colorStatus no esté vacío para obtener la información
        //según estado
        var status = (colorStatus === '') ? '' : statusDetail.find(s => s[event.estado])
        //Variable que contiene la información del estado y tooltip
        //Siempre y cuando el color status no esté vacío
        var flagIcon = (colorStatus === '') ? '\u00A0' : (
            <div>
                <i style={{"cursor": "pointer"}} data-tip data-for={randomValue}
                    className={`spriteSemaforo spriteSemaforo-${colorStatus}`}></i>
                <ReactTooltip id={randomValue} aria-haspopup='true' role='info'>
                    <p><strong>{status[event.estado]}</strong></p>
                    <p>{status.descripcion}</p>
                    <p><strong>Duración:{'\u00A0'}</strong>{GeneralHelper.secondsToHMS(event.duracion_evento)}</p>
                </ReactTooltip>
            </div>

        )
        //Finalmente se retorna la variable
        return flagIcon
    }

    /**
     * Función enfocada en transformar los segundos en formato: hh:mm:ss
     * @param {Integer} totalSeconds Variable que contiene el total de segundos
     *                               de una medición.
     * @return {String} Retorna un String con la información formateada.
     */
    static secondsToHMS(totalSeconds) {
        var days = Math.floor(totalSeconds / 86400)
        var hours = Math.floor((totalSeconds % 86400) / 3600)
        var minutes = Math.floor(((totalSeconds % 86400) % 3600) / 60)
        var seconds = Math.round(((totalSeconds % 86400) % 3600) % 60)
        //Validar visualización días
        let result = (Math.round(days) > 0 ? (Math.round(days) + " día(s) ") : "")
        result += (hours < 10 ? "0" + hours : hours);
        result += ":" + (minutes < 10 ? "0" + minutes : minutes);
        result += ":" + (seconds  < 10 ? "0" + seconds : seconds);
        return result
    }

    /**
     * Función enfocada en obtener un dígito de un texto.
     * @param {String} text Texto a inspeccionar.
     * @return {Integer} Retorna el número si se encuentra, y 0 si hay algún problema
     */
    static getIntegerFromStringWithRegex(text){
        try{
            return parseInt(text.match(/\d+/)[0])
        }catch(e){
            return 0
        }
    }

    /**
     * Función enfocada en validar si la cantidad de columnas en vista es un
     * número, además de que la cantidad de objetos  - sean nodos u objetivos -
     * no deben superar la cantidad definida para aparecer en pantalla.
     * @param {Integer} columnInView Número de columnas en vista
     * @param {Integer} objectsToShowInColumnsCount Número de objetos que
                                                    aparecerán en pantalla.
     * @return {Integer} Devuelve la cantidad de columnas en vista si es
     *                   efectivamente un número, además de que no debe ser mayor
     *                   a los objetos que aparecen en pantalla y no sea mayor a
     *                   5, en caso contrario devuelve 3.
     */
    static columnInViewIsValid(columnInView, objectsToShowInColumnsCount){
        //Primero se valida si la columna en vista es un número,
        //sino lo es por defecto se visualizan 3 columnas.
        return Number.isInteger(columnInView) ? (
            //Aquí se valida que las columas en pantalla no pueden ser mayor a
            //la cantidad de objetos que se visualizan.
            //Si no es mayor entonces se considera la cantidad de columnas en pantalla
            (columnInView < objectsToShowInColumnsCount) ? columnInView : (
                //En caso de que no entonces se valida que no sea mayor a 5
                //ya que sedebe de considerar si son muchos objetos
                (objectsToShowInColumnsCount > 5)? 5 : objectsToShowInColumnsCount
            )
        ) : 3
    }

    /**
     * Función que valida si una cadena es un color definido en hexadecimal.
     * @param {String} stringColorHex Texto de color en haxadecimal
     * @return {Boolean} Devuelve true si es correcto, false en caso contrario.
    */
    static ifStringIsColorHex(stringColorHex){
        const re = /[0-9A-Fa-f]{6}/g
        return (re.test(stringColorHex)) ? true : false
    }
}



