import { REQUEST_SUCCESS, REQUEST_ERROR} from '../actions/FlagActions'

/**
 * Función anónima encargada de manejar los estados de la aplicación
 * para el caso de contenedor de semáforo
 * @param {Object} state Se define por defecto null para cuando se realice la
                         petición se cambie por otro
 * @param {Object} action Se guarda la información manejada en el reducer
 * @return {String} Si se realiza la petición, se devuelve la información del
                    semáforo en json string, en caso contrario null.
 */
export default function (state = null, action) {
    switch (action.type) {
        case REQUEST_SUCCESS:
            return action.body;
        case REQUEST_ERROR:
            return action.body;
            break;
    }
    return state;
}



