import React from 'react'
import ReactTooltip from 'react-tooltip'
import FlagTableBodyCell from '../FlagTableBodyCell.js'
import classes from '../../../css/styles.css'
import CreateStylesHelper from '../../helpers/CreateStylesHelper.js'
import GeneralHelper from '../../helpers/GeneralHelper.js'

/**
 * InvertedTableBody Componente que se encarga de renderizar la información
 * del cuerpo de la tabla invertida.
 */
export default class InvertedTableBody extends React.Component{

    constructor(){
        super()
    }

    render(){
        //Obtener información de metadata
        const metadata = this.props.metadata
        //Obtener información de los estilos definidos para cabecera
        const nodeTitles = metadata.design.header_titles
        //Estilos para objetivos de columna principal
        const mainColumnTitleStyles = CreateStylesHelper.createColumnTitleComponentInvertedStyles(
                                        metadata.design.main_column_title,
                                        metadata.design,
                                        "main")
        //Estilos definidos para la segunda columna
        const secondColumnStyles = CreateStylesHelper.createColumnTitleComponentInvertedStyles(
                                        metadata.design.main_column_title,
                                        metadata.design,
                                        "second")
        //Obtener la información de los objetivos
        const objectives = this.props.objectives
        //Obtener información de los nodos
        const nodes = this.props.nodes
        //Obtener información de los servicios
        const services = this.props.services
        //Obtener la información de las plantillas definidas
        const template = this.props.template
        //Obtener la cantidad de celdas vacías
        const emptyCells = this.props.emptyCells
        //Variable en donde se guarda el JSX para rendarizar información de los
        //nodos, además de los estados por objetivo y los espacios en blanco
        const nodeItems = nodes.map((node, index) =>
            <tr key={`node_row_${index}`}>
                <td style={mainColumnTitleStyles}
                    className={`${classes['title']}`}
                key={node.nodo_id}>
                    {this.setNodeNameAndTooltip(node)}
                </td>
                <td style={secondColumnStyles}
                    className={`${classes['title-subtitle']} ${classes['truncate-string']}`}>
                {node.subtitulo}
                </td>
                {
                    objectives.map((objective) =>
                        <FlagTableBodyCell
                            key={`body_cell_${node.nodo_id}_${objective.objetivo_id}`}
                            objective={objective}
                            nodeId={node.nodo_id}
                            cellKey={`${node.nodo_id}_${objective.objetivo_id}`}
                            status={template.estados}
                            metadata={metadata}
                            />
                    )
                }
                {GeneralHelper.createBlankSpaces(node.nodo_id,
                                                emptyCells,
                                                this.props.blankWidthStyle
                                                )}
            </tr>
        )
        //Finalmente se retorna JSX con el cuerpo de la tabla
        return(
            <tbody>
                {nodeItems}
            </tbody>
        )
    }

    /**
     * Función destinada a crear el JSX destinado a renderizar la información
     * de los nodos, correspondientes al título y el sub-título.
     * @param {Object} node Variable donde se guarda la información de nodos.
     * @return {Object} Devuelve un objeto JSX que contiene el html generado.
     */
    setNodeNameAndTooltip(node){
        //Generar un valor aleatorio para relacionar el tooltip y el span contenedor
        //del nombre del nodo del titulo
        const randomValue = Math.random().toString(36).substr(2)
        //Obtener el título del nodo
        const nodeTitle = node.titulo
        //Obtener el subtítulo del nodo
        const nodeSubTitle = node.subtitulo
        //Obtener el id del nodo
        const nodeId = node.nodo_id
        //Se devuelve el JSX con la información respectiva
        return (
            <div key={nodeId} className={classes['truncate-string']}>
                <span key={`node_title_${nodeId}`} style={{"cursor": "pointer"}}
                    data-tip data-for={randomValue}>{nodeTitle}</span>
                <ReactTooltip id={randomValue} aria-haspopup='true' role='info'>
                    <p>{nodeTitle}</p>
                    <p>{nodeSubTitle}</p>
                </ReactTooltip>
            </div>
        )
    }
}



