import GeneralHelper from './GeneralHelper.js'

//Variable que guarda el color en hexadecimal por defecto para color de fuente
const DEFAULT_FONT_COLOR = '#000000'
//Variable que guarda el color en hexadecimal por defecto para color de fondo
const DEFAULT_BACKGROUND_COLOR = '#ffffff'
//Variable que guarda el color en hexadecimal por defecto para color de fondo
//de los controles
const DEFAULT_BACKGROUND_COLOR_CONTROLS = '#ff0000'
/**
 * CreateStylesHelper Clase de ayuda con la cual se permite crear Object
 *                    javascript con los estilos aptos para agregar como css.
 */

export default class CreateStylesHelper{

    /**
     * Función que se encarga de generar los estilos para las cabeceras de las
     * columnas.
     * @param {Object} headerTitles Contiene información relativa a los estilos
     *                              definidos en el metadata para los títulos de
     *                              las cabeceras.
     * @param {Object} design Contiene información del los estilos en general
     *                        definidos en la metadata.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createHeaderTitleStyles(headerTitles, design){
        const otherColumnSize = design.other_column_size
        const nodeTitleStyles = {
            "backgroundColor": GeneralHelper.ifStringIsColorHex(headerTitles["background-color"]) ?
                headerTitles['background-color'] : DEFAULT_BACKGROUND_COLOR,
            "width":  otherColumnSize,
            "maxWidth": otherColumnSize,
            "height": design["rows-height"],
            "maxHeight": design["rows-height"],
            "minHeight": design["rows-height"],
            "fontFamily": headerTitles["font"],
            "fontSize": headerTitles["font-size"],
            "color": GeneralHelper.ifStringIsColorHex(headerTitles["font-color"]) ?
                headerTitles['font-color'] : DEFAULT_FONT_COLOR,
            "fontSize": headerTitles["font-size"],
            "textAlign": headerTitles["text-align"]
        }
        return nodeTitleStyles
    }

    /**
     * Función que se encarga de generar los estilos para la primera cabecera.
     * @param {Object} mainColumnTitle Contiene información relativa a los estilos
     *                                 definidos en el metadata para la primera
     *                                 columna de la cabecera.
     * @param {Object} design Contiene información del los estilos en general
     *                        definidos en la metadata.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createMainColumnTitleStyles(mainColumnTitle, design){
        const mainColumnTitleWidth = design.main_column_size
        const nodeTitleStyles = {
            "backgroundColor": GeneralHelper.ifStringIsColorHex(mainColumnTitle["background-color"]) ?
                mainColumnTitle["background-color"] : DEFAULT_BACKGROUND_COLOR,
            "width": mainColumnTitleWidth,
            "maxWidth": mainColumnTitleWidth,
            "fontSize": mainColumnTitle["font-size"],
            "height": design["rows-height"],
            "maxHeight": design["rows-height"],
            "minHeight": design["rows-height"],
            "fontFamily": mainColumnTitle["font"],
            "color": GeneralHelper.ifStringIsColorHex(mainColumnTitle["font-color"]) ?
                mainColumnTitle["font-color"] : DEFAULT_FONT_COLOR,
            "textAlign": mainColumnTitle["text-align"]
        }
        return nodeTitleStyles
    }

    /**
     * Función que se encarga de generar los estilos para la primera cabecera.
     * @param {Object} mainColumnTitle  Contiene información relativa a los estilos
     *                                  definidos en el metadata para la primera
     *                                  columna de la cabecera.
     * @param {Object} design Contiene información del los estilos en general
     *                        definidos en la metadata.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createMainColumnTitleComponentStyles(mainColumnTitle, design){
        const mainColumnTitleWidth = design.main_column_size
        const nodeTitleStyles = {
            "backgroundColor": GeneralHelper.ifStringIsColorHex(mainColumnTitle["background-color"]) ?
                mainColumnTitle["background-color"] : DEFAULT_BACKGROUND_COLOR,
            "width": mainColumnTitleWidth,
            "maxWidth": mainColumnTitleWidth,
            "height": design["rows-height"],
            "maxHeight": design["rows-height"],
            "minHeight": design["rows-height"],
            "fontFamily": mainColumnTitle["font"],
            "color": GeneralHelper.ifStringIsColorHex(mainColumnTitle["font-color"]) ?
                mainColumnTitle["font-color"] : DEFAULT_FONT_COLOR,
            "fontSize": mainColumnTitle["font-size"],
            "textAlign": mainColumnTitle["text-align"]
        }
        return nodeTitleStyles
    }

    /**
     * Función que se encarga de generar los estilos para la primera cabecera
     * del semáforo invertido.
     * @param {Object} mainColumnTitle  Contiene información relativa a los estilos
     *                                  definidos en el metadata para la primera
     *                                  columna de la cabecera.
     * @param {Object} design Contiene información del los estilos en general
     *                        definidos en la metadata.
     * @param {Object} type Contiene una cadena con dos valores posibles:
     *                      - main -> 0.66
     *                      - second -> 0.34
     *                      Ya que se realiza una extración del porcentaje en
     *                      relación al total el tamaño definido para la columna
     *                      principal, para poder colocar dos celdas en el mismo
     *                      espacio.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createMainColumnTitleComponentInvertedStyles(mainColumnTitle, design, type){
        //Obtener la cantidad definida en int
        const mainColumnSize = GeneralHelper.getIntegerFromStringWithRegex(design.main_column_size)
        let mainColumnTitleWidth
        let titleStyles = {}
        switch (type) {
            case "main":
                mainColumnTitleWidth = Math.round(mainColumnSize * 0.66)
                break;
            case "second":
                mainColumnTitleWidth = Math.round(mainColumnSize * 0.34)
                //Se agrega este css para que la segunda columna se sitúe
                //al lado de la primera columna.
                titleStyles["marginLeft"] = Math.round(mainColumnSize * 0.66)
                break;
        }
        titleStyles["backgroundColor"] = GeneralHelper.ifStringIsColorHex(
            mainColumnTitle["background-color"]) ?
            mainColumnTitle["background-color"] : DEFAULT_BACKGROUND_COLOR
        titleStyles["width"] = mainColumnTitleWidth
        titleStyles["maxWidth"] = mainColumnTitleWidth
        titleStyles["height"] = design["rows-height"]
        titleStyles["maxHeight"] = design["rows-height"]
        titleStyles["minHeight"] = design["rows-height"]
        titleStyles["fontFamily"] = mainColumnTitle["font"]
        titleStyles["color"] = GeneralHelper.ifStringIsColorHex(
            mainColumnTitle["font-color"]) ?
            mainColumnTitle["font-color"] : DEFAULT_FONT_COLOR
        titleStyles["fontSize"] = mainColumnTitle["font-size"]
        titleStyles["textAlign"] = mainColumnTitle["text-align"]
        return titleStyles
    }

    /**
     * Función que se encarga de generar los estilos para las columnas que funcionan
     * como título en ambos tipos de semáforo:
     * - Normal -> Objetivos
     * - Invertido -> Nodos
     * @param {Object} columnTitle Estilos definidos para los títulos de las
     *                             columnas situados en la primera.
     * @param {Object} design Contiene información del los estilos en general
     *                        definidos en la metadata.
     * @param {Object} type Contiene una cadena con dos valores posibles:
     *                      - main -> 0.66
     *                      - second -> 0.34
     *                      Ya que se realiza una extración del porcentaje en
     *                      relación al total el tamaño definido para la columna
     *                      principal, para poder colocar dos celdas en el mismo
     *                      espacio.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createColumnTitleComponentInvertedStyles(columnTitle, design, type){
        //Obtener la cantidad definida en int
        const mainColumnSize = GeneralHelper.getIntegerFromStringWithRegex(design.main_column_size)
        let columnTitleWidth
        let titleStyles = {}
        switch (type) {
            case "main":
                columnTitleWidth = Math.round(mainColumnSize * 0.66)
                break;
            case "second":
                columnTitleWidth = Math.round(mainColumnSize * 0.34)
                titleStyles["marginLeft"] = Math.round(mainColumnSize * 0.66)
                break;
        }
        titleStyles["backgroundColor"] = GeneralHelper.ifStringIsColorHex(
            columnTitle["background-color"]) ?
            columnTitle["background-color"] : DEFAULT_BACKGROUND_COLOR
        titleStyles["width"] = columnTitleWidth
        titleStyles["maxWidth"] = columnTitleWidth
        titleStyles["height"] = design["rows-height"]
        titleStyles["borderLeft"] = "1px solid #ddd"
        titleStyles["maxHeight"] = design["rows-height"]
        titleStyles["minHeight"] = design["rows-height"]
        titleStyles["fontFamily"] = columnTitle["font"]
        titleStyles["color"] = GeneralHelper.ifStringIsColorHex(
            columnTitle["font-color"]) ?
            columnTitle["font-color"] : DEFAULT_FONT_COLOR
        titleStyles["fontSize"] = columnTitle["font-size"]
        titleStyles["textAlign"] = columnTitle["text-align"]
        return titleStyles
    }

    /**
     * Función que se encarga de generar los estilos de las celdas que reflejan
     * los estados en el semáforo.
     * @param {Object} tableStatusRows Variable donde se define la información de la
     *                                 metadata.
     * @param {Object} design Contiene información del los estilos en general
     *                        definidos en la metadata.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createTableStatusRowStyles(tableStatusRows, design){
        const otherColumnSize = design["other_column_size"]
        const statusRowStyles = {
            "backgroundColor": GeneralHelper.ifStringIsColorHex(
                tableStatusRows["background-color"]) ?
                tableStatusRows["background-color"] : DEFAULT_BACKGROUND_COLOR,
            "width": otherColumnSize,
            "maxWidth": otherColumnSize,
            "height": design["rows-height"],
            "maxHeight": design["rows-height"],
            "minHeight": design["rows-height"],
            "textAlign": tableStatusRows["status-align"]
        }
        return statusRowStyles
    }

    /**
     * Función que se encarga de generar la información usada en la cabecera
     * del semáforo.
     * @param {Object} metadata Variable donde se define la información de la
     *                          metadata.
     * @param {String} elements Información de label elementos.
     * @return {Object} Devuelve un Object con la información apta para usar en
     *                  la cabecera.
     */
    static createFlagInfoMetadata(metadata, elements){
        const flagInfoMetadata = {
            "titulo": metadata.titulo,
            "logo": metadata.logo,
            "informacion": metadata.informacion,
            "columnas_en_pantalla": metadata.columnas_en_pantalla,
            "arrows-background-color": GeneralHelper.ifStringIsColorHex(
                metadata.design["arrows-background-color"]) ?
                metadata.design["arrows-background-color"]
                :
                DEFAULT_BACKGROUND_COLOR_CONTROLS,
            "other_column_size": metadata.design["other_column_size"],
            "elementos": elements,
            "main_column_size": metadata.design["main_column_size"]
        }
        return flagInfoMetadata
    }

    /**
     * Función que se encarga de generar los estilos para las celdas en blanco.
     * @param {Object} cellSize Variable donde se define el máximo de with para
     *                          las celdas.
     * @param {Integer} rowsHeight Variable donde se define el máximo de height
     *                             para las celdas.
     * @return {Object} Devuelve un Object con los estilos aptos para agregar
     *                  a los JSX.
     */
    static createBlankWidthStyle(cellSize, rowsHeight){
        const nodeTitleStyles = {
            "width": cellSize,
            "maxWidth": cellSize,
            "height": rowsHeight,
            "maxHeight": rowsHeight,
            "minHeight": rowsHeight,
        }
        return nodeTitleStyles
    }
}



