import React from 'react'
import classes from '../../../css/styles.css'
import ReactTooltip from 'react-tooltip'
import CreateStylesHelper from '../../helpers/CreateStylesHelper.js'
import GeneralHelper from '../../helpers/GeneralHelper.js'

/**
 * InvertedTableHead Componente que se encarga de renderizar la información
 * de la cabecera de la tabla invertida.
 */
export default class InvertedTableHead extends React.Component{

    constructor(){
        super()
    }

    render(){
        //Obtener información de objetivos
        const objectives = this.props.objectives
        //Obtener información de metadata
        const metadata = this.props.metadata
        //Obtener la información de los estilos definidos en metadata para la cabecera
        const headerTitles = metadata.design.header_titles
        //Crear los estilos para las cabeceras de la columnas en este caso objetivos
        const objectiveTitleStyles = CreateStylesHelper.createHeaderTitleStyles(
                                        headerTitles,
                                        metadata.design)
        //Crear los estilos para la primera columna
        const mainColumnTitleStyles = CreateStylesHelper.createMainColumnTitleComponentInvertedStyles(
                                        headerTitles,
                                        metadata.design,
                                        "main")
        //Crear los estilos para la segunda columna
        const secondColumnTitleStyles = CreateStylesHelper.createMainColumnTitleComponentInvertedStyles(
                                        headerTitles,
                                        metadata.design,
                                        "second")
        //Generar un valor aleatorio
        const randomValue = Math.random().toString(36).substr(2)
        //Obtener información de template definida para objetivo
        const objectiveTitle = this.props.objectiveTitle
        //Variable para guardar información de los objetivos
        const objectiveItems = objectives.map((objective) =>
            this.createObjectiveItems(objective, objectiveTitleStyles)
        )
        //Constante para las celdas vacías
        const emptyCellsContainer = GeneralHelper.createBlankSpaces("th",
                                        this.props.emptyCells,
                                        this.props.blankWidthStyle
                                        )
        //Finalmente se retorna la cabecera de la tabla
        return(
            <thead>
                <tr>
                    <th key={"objetive_th_0"} style={mainColumnTitleStyles}
                        className={`${classes['title']} ${classes['truncate-string']}`}
                    ><div key={objectiveTitle}>{objectiveTitle}</div></th>
                    <th key={"objetive_th_1"} style={secondColumnTitleStyles}
                        className={`${classes['title-subtitle']}`}>
                    </th>
                    {objectiveItems}
                    {emptyCellsContainer}
                </tr>
            </thead>
        )
    }

    createObjectiveItems(objective, objectiveTitleStyles){
        const randomValue = Math.random().toString(36).substr(2)
        return (
            <th style={objectiveTitleStyles}
                data-tip data-for={randomValue}
                className={classes['rows']}
                key={`objective_th_${objective.objetivo_id}`}>
                    {(objective.nombre === '')?'\u00A0' : (
                        <div style={{"cursor": "pointer"}}
                             className={classes['truncate-string']}
                             key={`objective_title_${objective.objetivo_id}`}>
                        {objective.nombre}
                        <ReactTooltip id={randomValue} aria-haspopup='true' role='info'>
                            <p>{objective.nombre}</p>
                        </ReactTooltip>
                        </div>
                        )
                    }
            </th>
        )
    }
}



