import React from 'react'
import ReactTooltip from 'react-tooltip'
import FlagTableBodyCell from '../FlagTableBodyCell.js'
import classes from '../../../css/styles.css'
import CreateStylesHelper from '../../helpers/CreateStylesHelper.js'
import GeneralHelper from '../../helpers/GeneralHelper.js'

/**
 * NormalTableBody Componente que se encarga de renderizar la información
 * del cuerpo de la tabla.
 */
export default class NormalTableBody extends React.Component{

    constructor(){
        super()
    }

    render(){
        //Obtener información de metadata
        const metadata = this.props.metadata
        //Obtener información de los estilos definidos para cabecera
        const headerTitles = metadata.design.header_titles
        //Estilos para columnas de subtitulos
        const subTitleStyles = CreateStylesHelper.createHeaderTitleStyles(
                                        headerTitles,
                                        metadata.design)
        //Estilos definidos para la primera columna de subtitulo
        const mainColumnSubTitleStyles = CreateStylesHelper.createMainColumnTitleStyles(
                                        headerTitles,
                                        metadata.design)
        //Estilos para objetivos de columna principal
        const mainColumnTitleObjectiveStyles = CreateStylesHelper.createMainColumnTitleComponentStyles(
                                        metadata.design.main_column_title,
                                        metadata.design)
        //Obtener la información de los objetivos
        const objectives = this.props.objectives
        //Obtener información de los nodos
        const nodes = this.props.nodes
        //Obtener la información de las plantillas definidas
        const template = this.props.template
        //Obtener la cantidad de celdas vacías
        const emptyCells = this.props.emptyCells
        //Obtener los estilos para las celdas en blanco
        const blankWidthStyle = this.props.blankWidthStyle
        //Variable en donde se guarda el JSX para rendarizar información de los
        //objetivos, además de los estados por nodo y los espacios en blanco
        const objectiveItems = objectives.map((objective, index) =>
            <tr key={`obj_row_${index}`}>
                <td style={mainColumnTitleObjectiveStyles}
                    className={`${classes['title']}`}
                key={objective.objetivo_id}>
                    {this.setObjectiveNameAndTooltip(objective, template.servicio)}
                </td>
                {
                    nodes.map((node) =>
                        <FlagTableBodyCell key={`body_cell_${objective.objetivo_id}_${node.nodo_id}`}
                                           cellKey={`${objective.objetivo_id}_${node.nodo_id}`}
                                           objective={objective}
                                           nodeId={node.nodo_id}
                                           status={template.estados}
                                           metadata={metadata}
                                           />
                    )
                }
                {GeneralHelper.createBlankSpaces(objective.objetivo_id, emptyCells, blankWidthStyle)}
            </tr>
        )
        //Variable para contener información de los subtitulos
        const subtitles = (
            <tr key={"subtitle_row"}>
                <td key={'subtitle_0'} style={mainColumnSubTitleStyles}
                    className={`${classes['title']} ${classes['truncate-string']}`}>
                &nbsp;</td>
                {
                    nodes.map((node, index) =>
                        <td key={`subtitle_${(index+1)}`}style={subTitleStyles} className={`${classes['truncate-string']} ${classes['rows']}`}>
                        {(node.subtitulo == "")? '\u00A0' : node.subtitulo}</td>
                    )
                }
                {GeneralHelper.createBlankSpaces("sub", emptyCells, blankWidthStyle)}
            </tr>
        )
        //Finalmente se retorna JSX con el cuerpo de la tabla
        return(
            <tbody>
                {subtitles}
                {objectiveItems}
            </tbody>
        )
    }

    /**
     * Función destinada a crear el JSX destinado a renderizar la información
     * de los objetivos, correspondientes al nombre y el servicio.
     * @param {Object} objective Variable que contiene información de los objetivos.
     * @param {Boolean} showService Variable en donde se indica si se debe mostrar
     *                  el servicio en el tooltip.
     * @return {Object} Devuelve un objeto JSX que contiene el html generado.
     */
    setObjectiveNameAndTooltip(objective, showService){
        //Generar un valor aleatorio para relacionar el tooltip y el span contenedor
        //del nombre del objetivo
        const randomValue = Math.random().toString(36).substr(2)
        //Obtener el nombre del objetivo
        const objectiveName = objective.nombre
        //Obtener el identificador de objetivo
        const objectiveId = objective.objetivo_id
        //Obtener el nombre del servicio según su id
        const serviceName = objective.servicio_nombre
        //Se devuelve el JSX con la información respectiva
        return (
            <div key={objective.objetivo_id} className={classes['truncate-string']}>
                <span key={`obj_name_${objectiveId}`} style={{"cursor": "pointer"}}
                    data-tip data-for={randomValue}>{objectiveName}</span>
                <ReactTooltip id={randomValue} aria-haspopup='true' role='info'>
                    <p>{objectiveName}</p>
                    {(showService) ? (<p>{serviceName}</p>): ""}
                </ReactTooltip>
            </div>
        )
    }

}



