import React from 'react'
import classes from '../../css/styles.css'
import GeneralHelper from '../helpers/GeneralHelper.js'

/**
 * FlagHeaderTableInfo componente usado para renderizar la información de cabecera
 * del semáforo.
 * Consta del título, logo, texto adicional y controles para las columas de la
 * tabla.
 */
export default class FlagHeaderTableInfo extends React.Component{
    
    //Para la extensión de columas por nodo u objetivos según tipo de semáforo
    width = 80

    constructor(){
        super()
        //Se vincula variable con método de la clase
        this.handleLeftClick = this.handleLeftClick.bind(this)
        //Se vincula variable con método de la clase
        this.handleRightClick = this.handleRightClick.bind(this)
    }

    /**
     * Método que se usa para manejar el clickeo de la flecha izquierda del control
     * del semáforo.
     * Básicamente lo que realiza es realizar un scroll hacia la izquierda según
     * la cantidad definida de columnas en pantalla, además de restar la cantidad
     * visualizada.
     */
    handleLeftClick(){
        //Obtener información de columnas visualizadas en pantalla
        const columnsInView = this.props.flagInfoMetadata.columnas_en_pantalla
        //Obtener información de la cantidad de columnas visualizados en la cabecera
        const columnCount = this.props.columnCount
        //Calcular el valor width de la cantidad de nodos que se visualizan al
        //cargar el semáforo por primera vez
        const scrollValue = columnsInView * this.width
        //Obtener el elemento
        const columnsInViewElement =  document.getElementById('columnas-en-vista')
        //obtener números mostrados actualmente
        const columnsInViewCount = columnsInViewElement.innerHTML.split("-")
        //restar valores siempre y cuando no sean los primeros
        if(columnsInViewCount[0] != "1"){
            //Se corre el scroll hacia la izquierda
            document.getElementById('div-table-container').scrollLeft -= scrollValue
            //Se pregunta si el primer valor es distinto al total
            if(parseInt(columnsInViewCount[1]) <= columnCount){
                //Si es así entonces en ambos caso se realiza una resta normal
                //Con la información de nodos por pantalla
                columnsInViewElement.innerHTML =
                `${parseInt(columnsInViewCount[0]-columnsInView)}-${parseInt(columnsInViewCount[1])-columnsInView}`
            }else{
                //En caso contrario se saca la cantidad máxima de nodos en pantalla
                //Según el total de nodos disponibles
                const maxColumnsInView = (parseInt(columnCount/columnsInView)) * columnsInView
                //Y por consiguiente se restablece para poder seguir presionando
                //hacia atrás
                columnsInViewElement.innerHTML =
                    `${(maxColumnsInView-columnsInView)+1}-${maxColumnsInView}`
            }
        }
    }

    /**
     * Método que se usa para manejar el clickeo de la flecha derecha del control
     * del semáforo.
     * Básicamente lo que realiza es realizar un scroll hacia la derecha según
     * la cantidad definida de columnas en pantalla, además de sumar la cantidad
     * visualizada.
     */
    handleRightClick(){
        //Obtener información de columnas visualizadas en pantalla
        const columnsInView = this.props.flagInfoMetadata.columnas_en_pantalla
        //Obtener información de la cantidad de columnas visualizados en la cabecera
        const columnCount = this.props.columnCount
        //Calcular el valor width de la cantidad de nodos que se visualizan al
        //cargar el semáforo por primera vez
        const scrollValue = columnsInView * this.width
        /*
         * Lógica para cambiar label de monitores mostrados en pantalla
         */
        //Obtener el elemento
        const columnsInViewElement =  document.getElementById('columnas-en-vista')
        //obtener números mostrados actualmente
        const columnsInViewCount = columnsInViewElement.innerHTML.split("-")
        //siguiente valor inicial
        const columnInitValue = columnsInView + parseInt(columnsInViewCount[0])
        //siguiente valor final
        const columnFinalValue = columnsInView + parseInt(columnsInViewCount[1])
        //Si el valor actual es distinto a la cantidad total de nodo entonces
        //realizar operaciones
        if(parseInt(columnsInViewCount[1]) != columnCount){
            //Validar que si está dentro del rango para reemplazar el valor final por cantidad de nodo
            if(columnCount >= columnInitValue && columnCount <= columnFinalValue){
                //Se setea por defecto el valor inicial siguiente y
                //la cantidad de nodos
                columnsInViewElement.innerHTML =
                    `${columnInitValue}-${columnCount}`
            }else{
                //En caso contrario se setea por defecto el valor inicial siguiente
                //y el valor final siguiente
                columnsInViewElement.innerHTML =
                `${columnInitValue}-${columnFinalValue}`
            }
            //Se corre el scroll hacia la derecha
            document.getElementById('div-table-container').scrollLeft += scrollValue
        }
    }

    handleWidth(){
        var otherColumnSize = this.props.flagInfoMetadata.other_column_size
        var otherColumnSizeDigit = otherColumnSize.match(/[0-9]+/g)
        this.width = ((otherColumnSizeDigit === null) ? 80 :
            parseInt(otherColumnSizeDigit[0]))
        if (isNaN(this.width)) {
            this.width = 80
        }
    }

    render(){
        this.handleWidth()
        //Obtener la información de metadata
        const flagInfoMetadata = this.props.flagInfoMetadata
        //Obtener la información de columnas visualizadas en pantalla
        const columnsInView = flagInfoMetadata.columnas_en_pantalla
        //Calcular el valor width de la cantidad de nodos que se visualizan al
        //cargar el semáforo por primera vez
        const scrollValue = columnsInView * this.width
        //Variable en donde se define el texto que aparece indicando la cantidad
        //de columnas disponibles
        const columnInViewValue = `1-${columnsInView}`
        //Variable en donde se define el color de fondo de los controles
        const arrowsBackgroundColor = {
            "backgroundColor": flagInfoMetadata["arrows-background-color"]
        }
        const mainColumnSize = GeneralHelper.getIntegerFromStringWithRegex(flagInfoMetadata["main_column_size"])
        //Se retorna un div con toda la información de la cabecera
        //Se destaca que el width se define según el largo del título más la cantidad
        //de los nodos que se visualizan por primera vez.
        //Además se le setea un tamaño por defecto a la imagen y si está vacía
        //no se muestra nada.
        return (
            <div style={{"textAlign": "center", "width": `${(scrollValue+mainColumnSize)}px`}}>
                <h1>{flagInfoMetadata.titulo}</h1>
                <div>
                    {(flagInfoMetadata.logo == "") ? "" : (
                        <p><img src={flagInfoMetadata.logo} title={flagInfoMetadata.logo} style={
                            {
                                "width": "200px",
                                "height": "50px"
                            }
                        }/></p>
                    )}
                    <p>{flagInfoMetadata.informacion}</p>
                </div>
                <div style={arrowsBackgroundColor}
                    className={classes["arrow-background"]}
                     onClick={this.handleRightClick}>
                    <i className={"spriteButton spriteButton-arrow_right"}></i>
                </div>
                <div style={arrowsBackgroundColor}
                     className={`${classes["arrow-background"]} ${classes["margin-right-5px"]}`}
                     onClick={this.handleLeftClick}>
                    <i className={"spriteButton spriteButton-arrow_left"}></i>
                </div>
                <div style={{"float": "right",
                             "marginRight": "10px",
                             "marginTop": "5px"
                         }}>
                <strong id={"columnas-en-vista"}>{columnInViewValue}</strong>&nbsp;de&nbsp;<strong>{this.props.columnCount}</strong>&nbsp;{flagInfoMetadata.elementos}</div>
            </div>
        )
    }
}



