import 'whatwg-fetch'
//Variable para identificar que hubo una petición correcta
export const REQUEST_SUCCESS = 'REQUEST_SUCCESS'
//Variable para identificar que hubo una petición errónea
export const REQUEST_ERROR = 'REQUEST_ERROR'

//Variable que guarda la información resultado de la consulta mediante la
//ejecución de una función anónima
export const flagsData = (token)  => {
    //Devuelve un dispath para ser manejado en el contenedors
    return dispatch =>
    {
        dispatch(
            //Realizar la consulta mediante fetch
            fetch(`https://api.atentus.com:8082/public/semaforo/?format=json&token=${token}`, {
                method: 'get',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
            })
            .then(response => {
                //Si la respuesta es correcta y al menos está debajo del tipo error
                //300
                if (response.status >= 200 && response.status < 300) {
                    //Se devuelve la respuesta en json
                    return response.json();
                } else {
                    //En caso contrario se devuelve un error con el código asociado
                    return({
                        "error": response.status
                    })
                }
            })
            .then(function(data){
                //Se pregunta sino existe la key error para descartar que el
                //error es correcto
                if(!('error' in data)){
                    //Se devuelve que la petición fue correcta más su contenido
                    //en json string
                    return({
                        type: REQUEST_SUCCESS,
                        body: JSON.stringify(data)
                    })
                }else{
                    //En caso contrario se devuelve que la petición fue errónea
                    //con su código respectivo
                    return({
                        type: REQUEST_ERROR,
                        body: data.error
                    })
                }
            })
            .catch(error => {
                //En caso de cualquier problema que se presente se devuelve
                //automáticamente que la petición fue errónea con status 403
                return({
                    type: REQUEST_ERROR,
                    body: 403
                })
            })
        );
    }
}



