import React from 'react'
import classes from '../../../css/styles.css'
import NormalTableHead from './NormalTableHead.js'
import NormalTableBody from './NormalTableBody.js'
import GeneralHelper from '../../helpers/GeneralHelper.js'

/**
 * NormalTable componente usado para renderizar la información de la tabla
 * del semáforo.
 * Consta de la cabecera de la tabla y su cuerpo.
 */
export default class NormalTable extends React.Component{

    render(){
        //Se obtiene la información parseada desde la api
        var dataParsed = this.props.data
        //Se obtiene información de nodos
        var nodes = dataParsed.nodos
        //Se obtiene información de objetivos
        var objectives = dataParsed.objetivos
        //Se obtiene información de servicios
        var services = dataParsed.servicios
        //Se obtiene información de metadata
        var metadata = this.props.metadata
        //Definir columnas en pantalla
        var columnInView = metadata.columnas_en_pantalla
        var otherColumnSize = metadata.design.other_column_size
        var otherColumnSizeDigit = otherColumnSize.match(/[0-9]+/g)
        var width = ((otherColumnSizeDigit === null) ? 80 :
            parseInt(otherColumnSizeDigit[0]))
        if(isNaN(width)){
            width = 80
        }
        //Se calcula la cantidad de columnas en pantalla en píxeles
        let scrollValue = columnInView * otherColumnSizeDigit
        //Se obtiene la cantidad de celdas según nodos y columnas_en_pantalla
        var emptyCells = GeneralHelper.calculateEmptyCells(nodes.length,
                                                    columnInView)
        //Se obtiene información de template
        var template = dataParsed.usuario.template
        //Se obtiene información de los estilos para las celdas en blanco
        var blankWidthStyle = this.props.blankWidthStyle
        //Se retorna la tabla en sí
        //Cosas a destacar:
        //Al div se le define un width según la cantidad visualizada en pantalla.
        //Además se establece un margen izquierdo para correr las columnas donde
        //se visualiza la información de los estados del semáforo.
        return(
            <div style={{
                            "width": `${scrollValue}px`,
                            "marginLeft" : metadata.design.main_column_size
                        }}
                 id={"div-table-container"}
                 className={`panel panel-default ${classes['table-container']}`}>
                <table className={"table table-striped table-condensed"}>
                    <NormalTableHead  nodes={nodes}
                                    emptyCells={emptyCells}
                                    objectiveTitle = {template.objetivos}
                                    metadata={metadata}
                                    blankWidthStyle={blankWidthStyle}
                                    />
                    <NormalTableBody  objectives={objectives}
                                    nodes={nodes}
                                    services={services}
                                    status={dataParsed.estados}
                                    metadata={metadata}
                                    template={template}
                                    emptyCells={emptyCells}
                                    blankWidthStyle={blankWidthStyle}
                                    />
                </table>
            </div>
        )
    }
}



