/**
 * JSONConfigParser Clase de ayuda enfocada en validar la estructura de la
 *                  configuración del template y metadata.
 */
export default class JSONConfigParser {

    /**
     * Función enfocada en validar la configuración del template, principalmente
     * en su estructura.
     * Básicamente realiza un contador si el key definido es correcto y al final
     * se pregunta si el conteo final es equivalente a lo que debería de dar
     * para ser true
     * @param {Object} requestJSONTemplate Información de template parseada con
     *                                     objeto JSON.
     * @return {Boolean} Devuelve true si es correcto, false en caso contrario.
     */
    static validateTemplateConfig(requestJSONTemplate){
        var validJSONTemplate = {
            "servicio": "",
            "estados": [
                {
                    "descripcion": "",
                    "uptime": ""
                },
                {
                    "descripcion": "",
                    "downtime_global": ""
                },
                {
                    "downtime_parcial": "",
                    "descripcion": ""
                },
                {
                    "descripcion": "",
                    "sin_informacion": ""
                },
                {
                    "descripcion": "",
                    "evento_cliente": ""
                }
            ],
            "objetivos": "",
            "elementos": ""
        }
        try{
            var count = 0
            for(var key in validJSONTemplate){
                if(key in requestJSONTemplate){
                    count++
                }
                if(key == "estados"){
                    for(var status in requestJSONTemplate[key]){
                        var statusObject = requestJSONTemplate[key][status]
                        if ("descripcion" in statusObject){
                            if ("sin_informacion" in statusObject) {
                                count++
                            } else if ("downtime_parcial" in statusObject) {
                                count++
                            } else if ("downtime_global" in statusObject) {
                                count++
                            } else if ("uptime" in statusObject) {
                                count++
                            } else if ("evento_cliente" in statusObject) {
                                count++
                            }
                        }
                    }
                }
            }
            if(count == 8){
                return true
            }else{
                return false
            }
        }catch(err){
            return false
        }
    }

    /**
     * Función enfocada en validar la configuración de la metadata, principalmente
     * en su estructura.
     * Básicamente realiza un contador si el key definido es correcto y al final
     * se pregunta si el conteo final es equivalente a lo que debería de dar
     * para ser true.
     * @param {Object} requestJSONTemplate Información de la metadata parseada con
     *                                     objeto JSON.
     * @return {Boolean} Devuelve true si es correcto, false en caso contrario.
     */
    static validateMetaDataConfig(requestJSONMetadata){
        var validJSONMetadata = {
            "footer": {
                "url": "",
                "texto": ""
            },
            "informacion": "",
            "columnas_en_pantalla": 0,
            "design": {
                "main_column_title": {
                    "font-color": "",
                    "font-size": "",
                    "font": "",
                    "background-color": "",
                    "text-align": ""
                },
                "arrows-background-color": "",
                "header_titles": {
                    "font-color": "",
                    "font-size": "",
                    "font": "",
                    "background-color": "",
                    "text-align": ""
                },
                "table_status_rows": {
                    "background-color": "",
                    "status-align": ""
                },
                "other_column_size": "",
                "footer-font-color": "",
                "main_column_size": "",
                "flag-margin-left": "",
                "rows-height": "",
                "page-background-color": ""
            },
            "logo": "",
            "titulo": ""
        }
        try{
            var count = 0
            for(var key in validJSONMetadata){
                if(key in requestJSONMetadata){
                    count++
                }

                if(key == "footer"){
                    for(var footer in validJSONMetadata[key]){
                        if(footer in requestJSONMetadata[key]){
                            count++
                        }
                    }
                }

                if(key == "design"){
                    for(var design in validJSONMetadata[key]){
                        if(design in requestJSONMetadata[key]){
                            count++
                        }
                        for(var designInternalVal in validJSONMetadata[key][design]){
                            if(designInternalVal in requestJSONMetadata[key][design]){
                                count++
                            }
                        }
                    }
                }
            }
            if(count == 30){
                return true
            }else{
                return false
            }
        }catch(err){
            return false
        }
    }
}



