import 'babel-polyfill'
import React from 'react'
import ReactDOM from "react-dom"
import {Provider} from 'react-redux'
import {createStore, applyMiddleware} from 'redux'
import FlagContainer from "./containers/FlagContainer.js"
import allReducers from './reducers'
import thunk from 'redux-thunk'
import promise from 'redux-promise'
import createLogger from 'redux-logger'

//Logger para realizar debug de la aplicación
//const logger = createLogger();

//Create store para todos los estados
const store = createStore(allReducers,
    applyMiddleware(thunk, promise)
);

//Renderizar la vista completa, proveyéndole de un store
//para la información del semáoforo
ReactDOM.render(
    <Provider store={store}>
        <FlagContainer />
    </Provider>,
    document.getElementById('root')
);



