import React from 'react'
import classes from '../../css/styles.css'
import CreateStylesHelper from '../helpers/CreateStylesHelper.js'
import GeneralHelper from '../helpers/GeneralHelper.js'

/**
 * FlagTableBodyCell Componente destinado a renderizar el detalle de los
 *                   estado de las celdas en cada tipo de semáforo.
 */
export default class FlagTableBodyCell extends React.Component{

    constructor(){
        super()
    }

    render(){
        //Obtener información de objetivos
        var objective = this.props.objective
        //Obtener información de nodo id
        var nodeId = this.props.nodeId
        //Obtener información de metadata
        var metadata = this.props.metadata
        //Obtener información de intervalo valor
        var intervalValue = objective.intervalo_valor
        //Definir los estilos de las celtas que contienen estado
        var tableStatusRows = CreateStylesHelper.createTableStatusRowStyles(
                                    metadata.design.table_status_rows,
                                    metadata.design
                                )
        let event = ""
        //Validar si los nodos que vienen dentro del objetivo
        //están dentro de la lista de nodos disponible para renderizar el estado
        if(objective.nodos.length > 0){
            for (var objNodes of objective.nodos){
                if(objNodes.nodo_id == nodeId){
                    event = objNodes
                    break
                }
            }
        }
        //Definir el color del estado
        var colorStatus = GeneralHelper.setColorStatus(event.estado)
        //Definir el JSX del ícono y tooltip
        var flagIconAndTooltip = GeneralHelper.setFlagIconAndTooltip(
                                        this.props.status,
                                        event,
                                        colorStatus)
        //Devolver JSX con la celda correspondiente
        return(
            <td key={this.props.cellKey}
                style={tableStatusRows}
                className={classes['rows']}>
                {flagIconAndTooltip}
            </td>
        )
    }
}



