import React from 'react'
import classes from '../../css/styles.css'
import NormalTableBuilder from '../components/normal_table/NormalTableBuilder.js'
import InvertedTableBuilder from '../components/inverted_table/InvertedTableBuilder.js'
import GeneralHelper from '../helpers/GeneralHelper.js'

/**
 * Clase FlagTableSolver componente destinado a administrar la renderización de
 * los tipos de semáforos disponibles según preferencia de orientación del usuario
 */
export default class FlagTableSolver extends React.Component{

    constructor(){
        super()
    }

    render(){
        //Se obtiene la información parseada desde la api
        const data = this.props.data
        //Información de metadata
        let metadata = this.props.metadata
        //Información de columnas en pantalla
        const columnsInView = metadata.columnas_en_pantalla
        //Información de template
        const template = this.props.template
        //Se extrae la información de preferencia de orientación del semáforo
        const userOrientationPreference = data.usuario.preferencia_semaforo_orientacion
        //Si es 0 entonces se devuelve el componente encargado de la vista
        //por defecto
        if(userOrientationPreference == 0){
            //Objetos a mostrar en las columnas sucesivas de los estados
            //para semáforo normal
            const nodesCount = data.nodos.length
            //Resolver las columnas en pantalla según o anterior
            metadata.columnas_en_pantalla = GeneralHelper.columnInViewIsValid(
                columnsInView, nodesCount
            )
            //Se devuelve el componente por defecto
            return(
                <NormalTableBuilder data={data}
                                    metadata={metadata}
                                    template={template}
                                    />
            )
        }else{
            //Objetos a mostrar en las columnas sucesivas de los estados
            //para semáforo invertido
            const objectivesCount = data.objetivos.length
            //Resolver las columnas en pantalla según o anterior
            metadata.columnas_en_pantalla = GeneralHelper.columnInViewIsValid(
                 columnsInView, objectivesCount
            )
            //En caso contrario se devuelve el componente encargado de la
            //vista invertida
            return(
                <InvertedTableBuilder data={data}
                                    metadata={metadata}
                                    template={template}
                                    />
            )
        }
    }

}



