import React, {Component} from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {flagsData} from '../actions/FlagActions.js'
import FlagTableSolver from '../components/FlagTableSolver.js'
import FlagFooter from '../components/FlagFooter.js'
import CreateStylesHelper from '../helpers/CreateStylesHelper.js'
import JSONConfigParser from '../helpers/JSONConfigParser.js'
import classes from '../../css/styles.css'
require("../../css/styles.css")

/**
 * Clase FlagContainer para contener información de semáforo en general y
 * administrar estados a componentes en específico.
 */
class FlagContainer extends Component{

    componentWillMount(){
        //Ejecutar método para obtener los parámetros ger en json,
        //interpretados como Object javascript
        const params = this.paramsToJSON()
        //Ejecutar dispatch para la acción de obtener información de semáforo de
        //la Api respectiva
        this.props.dispatch(flagsData(params.token))
    }

    render(){
        //Variable para contener información de semáforo
        let flags = this.props.flags
        //Si es null entonces significa que no se ha ejecutado la petición
        if(flags === null){
            //Por lo tanto se retorna una imagen de cargado centrada
            return(
                <div><img src={"/static/vista_semaforo/img/loading-gray-large.gif"} style={{"marginLeft": "50%"}} /></div>
            )
        //En cambio de que sea número, significa que algún código
        //de error se ha enviado
        }else if (typeof flags === 'number'){
            //En tal caso si el estado es 403
            if(flags === 403){
                //Se retorna el mensaje de que debe revisar el token
                return(
                    <h5>Favor de revisar la autenticidad de token.</h5>
                )
            }else{
                //En caso contrario se devuelve un mensaje más genérico
                return(
                    <h5>No se puede obtener la información requerida. Inténtelo de nuevo.</h5>
                )
            }
        }else{
            /*
             * Si la respuesta es correcta se entra acá
             */
            //Se parsea la información obtenida desde la api
            const data = JSON.parse(flags)
            //Se extrae la metadata
            const metadata = data.usuario.metadata
            //Se extrae el template
            const template = data.usuario.template
            /*
             * Se valida:
             * 1.- Si la variable metadata viene vacía
             * 2.- Si el variable template viene vacía
             * 3.- Si está mal definida la estructura de template
             * 4.- Si está mal definida la estructura de metadata
             */
            if(Object.keys(metadata).length === 0 ||
                Object.keys(template).length === 0 ||
                !JSONConfigParser.validateTemplateConfig(template) ||
                !JSONConfigParser.validateMetaDataConfig(metadata)){
                    //De ser así se devuelve un mensaje al respecto
                    return(<div>La definición de metadata o template no está bien definida.</div>)
            }else{
                //En caso contrario es que está correcto y
                // Se setea el color de fondo
                document.body.style.backgroundColor = metadata.design["page-background-color"]
                //Finalmente se devuelve un componente donde se renderiza la información
                //del semáforo con el componente general FlagTableSolver, y el footer
                //que sería con el componente FlagFooter.
                //En ambos caso se les pasan variables.
                //En el caso del footer se se pasa lo que viene definido en metadata
                //y el color de la fuente
                return(
                    <div className={classes["container"]}
                        style={{"marginLeft": metadata.design["flag-margin-left"]}}>
                        <FlagTableSolver data={data}
                                         metadata={metadata}
                                         template={template}/>
                        <FlagFooter footer={metadata.footer}
                                    footerFontColor={metadata.design["footer-font-color"]} />
                    </div>
                )
            }
        }
    }

    /**
     * Función que permite obtener todas las variables get definidas como
     * Object javascript
     * @return {Object} Se devuelve un nuevo Object con los parámetros get declarados.
     */
    paramsToJSON(){
        const search = location.search.substring(1)
        let params = {}
        if(search != ""){
            params = JSON.parse('{"' + decodeURI(search).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g,'":"') + '"}')
        }
        return params
    }

}

/**
 * Función destinada a mapear los estados al objeto props del componente correspondiente.
 * @param {Object} state Es la información de flags que contiene la información
                         del semáforo.
 * @return {Object} Se devuelve un nuevo estado con la información de semáforo.
 */
function mapStateToProps(state) {
    return {
        flags: state.flags
    };
}

//Para conectar las acciones con FlagContainer
export default connect(mapStateToProps)(FlagContainer);



