import React, {Component} from 'react'
import classes from '../../css/styles.css'

/**
 * Clase FlagFooter es un componente destinado a renderizar la información
 * del pié de página del semáforo
 */
export default class FlagFooter extends Component{

    render(){
        //Se obtiene la propiedad footer
        const footer = this.props.footer
        //Se retorna un div con la información respectiva y aplicándole
        //el color de fuente definido, además de volver al link clickeable.
        return(
            <div className={`${classes['title']}`}>
                <p style={{
                    "color": this.props.footerFontColor
                }}><strong>{footer.texto}&nbsp;-&nbsp;<a href={footer.url}>{footer.url}</a></strong></p>
            </div>
        )
    }
}



