import React from 'react'
import classes from '../../../css/styles.css'
import ReactTooltip from 'react-tooltip'
import CreateStylesHelper from '../../helpers/CreateStylesHelper.js'
import GeneralHelper from '../../helpers/GeneralHelper.js'

/**
 * NormalTableHead Componente que se encarga de renderizar la información
 * de la cabecera de la tabla.
 */
export default class NormalTableHead extends React.Component{

    constructor(){
        super()
    }

    render(){
        //Obtener información de nodos
        const nodes = this.props.nodes
        //Obtener información de metadata
        const metadata = this.props.metadata
        //Obtener la información de los estilos definidos en metadata para la cabecera
        const headerTitles = metadata.design.header_titles
        //Crear los estilos para las cabeceras de la columnas
        const headerTitleStyles = CreateStylesHelper.createHeaderTitleStyles(
                                        headerTitles,
                                        metadata.design)
        //Crear los estilos para la primera columna
        const mainColumnTitleStyles = CreateStylesHelper.createMainColumnTitleStyles(
                                        headerTitles,
                                        metadata.design)
        //Generar un valor aleatorio
        const randomValue = Math.random().toString(36).substr(2)
        //Obtener información de template definida para objetivo
        const objectiveTitle = this.props.objectiveTitle
        //Variable para guardar información de los nodos
        const nodeItems = nodes.map((node) =>
            this.createNodeItemsAndTooltip(node, headerTitleStyles)
        )
        //Constante para las celdas vacías
        const emptyCellsContainer = GeneralHelper.createBlankSpaces("th",
                                        this.props.emptyCells,
                                        this.props.blankWidthStyle
                                        )
        //Finalmente se retorna la cabecera de la tabla
        return(
            <thead>
                <tr>
                    <th key={"node_0"} style={mainColumnTitleStyles}
                        className={`${classes['title']} ${classes['truncate-string']}`}
                    ><div key={objectiveTitle}>{objectiveTitle}</div></th>
                    {nodeItems}
                    {emptyCellsContainer}
                </tr>
            </thead>
        )
    }

    /**
     * Función enfocada en crear los item de los nodos de las columnas de las
     * cabecera, además de el tooltip asociado.
     * @param {Object} node Objeto que contiene información de un nodo.
     * @param {Object} headerTitleStyles Información de estilos asociados a la
     *                                   cabecera.
     * @return {Object} Devuelve un objeto JSX que contiene el html generado.
     */
    createNodeItemsAndTooltip(node, headerTitleStyles){
        const randomValue = Math.random().toString(36).substr(2)
        return (
            <th style={headerTitleStyles}
                data-tip data-for={randomValue}
                className={classes['rows']}
                key={`node_${node.nodo_id}`}>
                    {(node.titulo === '')?'\u00A0' : (
                        <div style={{"cursor": "pointer"}}
                             className={classes['truncate-string']}
                             key={`node_title_${node.nodo_id}`}>
                        {node.titulo}
                        <ReactTooltip id={randomValue} aria-haspopup='true' role='info'>
                            <p>{(node.titulo === '')?'\u00A0' : node.titulo}</p>
                            <p>{(node.subtitulo === '')?'\u00A0' : node.subtitulo}</p>
                        </ReactTooltip>
                        </div>
                        )
                    }
            </th>
        )
    }
}



