import React from 'react'
import CreateStylesHelper from '../../helpers/CreateStylesHelper.js'
import InvertedTable from './InvertedTable.js'
import FlagHeaderTableInfo from '../FlagHeaderTableInfo.js'

/**
 * InvertedTableBuilder destinado a ser el componente base para renderizar la
 * información base del semáforo invertido.
 */
export default class InvertedTableBuilder extends React.Component{

    render(){
        //Se obtiene la información parseada desde la api
        const data = this.props.data
        //Información de metadata
        const metadata = this.props.metadata
        //Información de template
        const template = this.props.template
        //Variable en donde se definen los estilos que llevan las celdas
        //que se se usan para la diferencia en las columnas finales
        const blankWidthStyle = CreateStylesHelper.createBlankWidthStyle(
                                metadata.design.other_column_size,
                                metadata.design["rows-height"])
        //Variable en donde se formatea la información que se necesita
        //en el componente InvertedTableInfo
        const flagInfoMetadata = CreateStylesHelper.createFlagInfoMetadata(
                                    metadata,
                                    template.elementos
                                )

        return (
        <div>
            <FlagHeaderTableInfo
                    flagInfoMetadata={flagInfoMetadata}
                    columnCount={data.objetivos.length} />
            <InvertedTable
                    data={data}
                    metadata={metadata}
                    blankWidthStyle={blankWidthStyle} />
        </div>
        )
    }
}



